import { FC, ReactElement, useEffect, useRef, useState } from 'react';
import USER from 'constants/user';
import Gradient from 'javascript-color-gradient';

import {
  TimelineFromNow,
  TimelineFromNowStamp,
  TimelineSnippetStamp,
  Wrapper,
  SnippetTitle,
  SnippetDescription,
  SnippetData,
  Container,
  TimestampVacancy
} from './style';

type Props = {};

const monthDiff = (dateFrom: Date, dateTo: Date): number => {
  return (
    dateTo.getMonth() -
    dateFrom.getMonth() +
    12 * (dateTo.getFullYear() - dateFrom.getFullYear())
  );
};

const Timeline: FC<Props> = (): ReactElement => {
  const [timelineMaxValue, setTimelineMaxValue] = useState<number>(0);
  const colors = useRef<string[]>([]);

  useEffect(() => {
    const timelineLengthByMonths = USER.education.reduce((prev, cur) => {
      const diff = monthDiff(cur.startDate, cur.endDate);

      return prev + diff;
    }, 0);

    /* eslint-disable */

    const globalStyles = window.getComputedStyle(document.body);
    const colorGradient = new Gradient();
    colorGradient.setGradient(
      globalStyles.getPropertyValue('--color-4'),
      globalStyles.getPropertyValue('--color-3')
    );
    colorGradient.setMidpoint(USER.education.length);
    colors.current = colorGradient.getArray();

    setTimelineMaxValue(timelineLengthByMonths);
  }, []);

  return (
    <Container>
      <Wrapper>
        {USER.education.map((entity, id, array) => (
          <div
            key={entity.id}
            style={{
              width: `${Math.round(
                (monthDiff(entity.startDate, entity.endDate) /
                  timelineMaxValue) *
                  100
              )}%`,
              color: colors.current[entity.id]
            }}
          >
            <SnippetData
              {...(id === array.length - 1 && {
                style: { whiteSpace: 'nowrap' }
              })}
            >
              <SnippetTitle>{entity.entityTitle}</SnippetTitle>
              <SnippetDescription color={colors.current[entity.id]}>
                {entity.entityDescription}
              </SnippetDescription>
            </SnippetData>
            <div
              style={{
                width: `100%`,
                backgroundColor: colors.current[entity.id],
                height: 20 * (id + 1)
              }}
            />
            <TimelineSnippetStamp>
              {entity.startDate.getFullYear()}
            </TimelineSnippetStamp>
          </div>
        ))}
        <TimelineFromNow
          style={{
            backgroundColor: colors.current[colors.current.length - 1],
            color: colors.current[colors.current.length - 1],
            height: 20 * colors.current.length
          }}
        >
          <TimelineFromNowStamp>
            <div
              style={{ position: 'relative', fontWeight: 'bold', fontSize: 13 }}
            >
              {new Date().getFullYear()}
              <div
                style={{
                  backgroundColor: colors.current[colors.current.length - 1]
                }}
              />
            </div>
          </TimelineFromNowStamp>
        </TimelineFromNow>
      </Wrapper>

      <div style={{ display: 'flex', marginTop: 10 }}>
        {USER.education.map((entity, id, array) => {
          return (
            <div
              key={entity.id}
              style={{
                display: 'flex',
                width: `${Math.round(
                  (monthDiff(entity.startDate, entity.endDate) /
                    timelineMaxValue) *
                    90 +
                    (id === array.length - 1 ? 20 : 0)
                )}%`
              }}
            >
              {entity.extra?.map((vacancy, index, array) => (
                <div
                  key={vacancy.id}
                  style={
                    !vacancy.year
                      ? { flex: 1 }
                      : {
                          width: `${
                            (12 / monthDiff(entity.startDate, entity.endDate)) *
                            100
                          }%`,
                          height: 24,
                          background: colors.current[entity.id],
                          filter: 'grayscale(1)',
                          opacity: (index + 1) / array.length
                        }
                  }
                />
              ))}
            </div>
          );
        })}
      </div>
      <div style={{ display: 'flex', marginTop: 48 }}>
        {USER.education.map((entity, id, array) => {
          return (
            <div
              key={entity.id}
              style={{
                display: 'flex',
                width: `${Math.round(
                  (monthDiff(entity.startDate, entity.endDate) /
                    timelineMaxValue) *
                    90 +
                    (id === array.length - 1 ? 20 : 0)
                )}%`
              }}
            >
              <div style={{ display: 'flex', flexDirection: 'column' }}>
                {entity.extra?.map(
                  (vacancy, index, array) =>
                    !vacancy.gap && (
                      <TimestampVacancy
                        key={vacancy.id}
                        color={colors.current[entity.id]}
                        opacity={(index + 1) / array.length}
                      >
                        {vacancy.description}
                      </TimestampVacancy>
                    )
                )}
              </div>
            </div>
          );
        })}
      </div>
    </Container>
  );
};

export default Timeline;
