import React from 'react';
import styled from 'styled-components';

const Video = styled.video`
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
`;

const NotFound = () => {
  return (
    <Video autoPlay muted loop id="notFound">
      <source src={`${process.env.PUBLIC_URL}/notfound.mp4`} type="video/mp4" />
    </Video>
  );
};

export default NotFound;
